<template>
  <!-- 车辆管理 -->
  <div class="container">
    <div class="header">
      <!-- <el-button type="primary" @click="addCarShow = true">添加车辆</el-button> -->
      <el-button type="primary">批量导出</el-button>
      <el-input v-model="input" placeholder="请输入车牌查询"></el-input>
      <el-date-picker
      v-model="timeAll"
      type="datetimerange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
      <!-- <el-button type="danger" @click="delList">批量删除</el-button> -->
    </div>
    <div class="con">
      <el-table
        :data="tableData"
        style="width: 100%"
        :row-class-name="tableRowClassName"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="entrainmentTime" label="夹带时间" width="180"></el-table-column>
        <el-table-column prop="entrainmentDescription" label="夹带描述" width="180"></el-table-column>
        <el-table-column prop="numberPlate" label="车牌号" width="120"></el-table-column>
        <el-table-column prop="type" label="车辆类型" width="120">
          <template slot-scope="scope">
            <el-tag size="mini" type="success" v-if="tableData[scope.$index].type=='0'">员工车辆</el-tag>
            <el-tag size="mini" type="warning" v-if="tableData[scope.$index].type=='1'">客户车辆</el-tag>
            <el-tag size="mini" type="danger" v-if="tableData[scope.$index].type=='2'">VIP车辆</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="owner" label="车辆所有人" width="110"></el-table-column>
        <el-table-column prop="driver" label="驾驶员" width="90"></el-table-column>
        <el-table-column prop="phone" label="驾驶员电话" width="120"></el-table-column>
        <el-table-column prop="ETCCode" label="ETC编码" width="120"></el-table-column>
        <el-table-column prop="ETCInCode" label="ETC内码" width="120"></el-table-column>
        <el-table-column prop="guarantor" label="企业担保人" width="120"></el-table-column>
        <el-table-column prop="enterprise" label="车辆归属企业" width="180"></el-table-column>
        <el-table-column prop="examination" label="车辆检查" width="80"></el-table-column>
        <el-table-column prop="createTime" label="创建时间" width="180"></el-table-column>
        <el-table-column prop="startTime" label="同行开始时间" width="180"></el-table-column>
        <el-table-column prop="endTime" label="同行结束时间" width="180"></el-table-column>
        <!-- <el-table-column prop="operat" fixed="right" label="操作" width="310">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="goCarInfo">查看</el-button>
            <el-button size="mini" type="warning" @click="modifyCarShow = true">修改</el-button>
            <el-popconfirm title="确定删除该车辆信息？">
              <el-button size="mini" slot="reference" type="danger">删除</el-button>
            </el-popconfirm>
            <el-button size="mini" type="primary" @click="goPassAnnal">检查记录</el-button>
          </template>
        </el-table-column> -->
      </el-table>
    </div>
    <div class="buttonCon">
      <el-pagination
        background
        layout="prev, pager, next"
        :total="1000">
        </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  name: "user",
  data() {
    return {
      timeAll:"",
      tableData: [
        {
          entrainmentTime: "2020.10.01",
          entrainmentDescription: "夹带保密性物品三盒",
          numberPlate: "陕A 88888899",
          type: "0",
          owner: "王小虎",
          driver: "驾驶员1",
          phone: "18888888888",
          ETCCode: "000000",
          ETCInCode: "000000",
          guarantor: "担保人",
          enterprise: "公司名称",
          examination: "20",
          createTime: "2016-05-04 02:02:02",
          startTime: "2016-05-04 02:02:02",
          endTime: "2016-05-04 02:02:02"
        },
        {
          entrainmentTime: "2020.10.01",
          entrainmentDescription: "夹带保密性物品三盒",
          numberPlate: "陕A 88888899",
          type: "0",
          owner: "王小虎",
          driver: "驾驶员1",
          phone: "18888888888",
          ETCCode: "000000",
          ETCInCode: "000000",
          guarantor: "担保人",
          enterprise: "公司名称",
          examination: "20",
          createTime: "2016-05-04 02:02:02",
          startTime: "2016-05-04 02:02:02",
          endTime: "2016-05-04 02:02:02"
        },
        {
          entrainmentTime: "2020.10.01",
          entrainmentDescription: "夹带保密性物品三盒",
          numberPlate: "陕A 88888899",
          type: "0",
          owner: "王小虎",
          driver: "驾驶员1",
          phone: "18888888888",
          ETCCode: "000000",
          ETCInCode: "000000",
          guarantor: "担保人",
          enterprise: "公司名称",
          examination: "20",
          createTime: "2016-05-04 02:02:02",
          startTime: "2016-05-04 02:02:02",
          endTime: "2016-05-04 02:02:02"
        }
      ],
    };
  },
  methods: {
    goCarInfo() {
      this.$router.push(`carInfo`)
    },
    goPassAnnal() {
      this.$router.push(`passAnnal`)
    },
    delList() {
      this.$message.error('请先选择需要删除的车辆');
    }
  },
};
</script>
<style scoped>
.header .el-button {
  margin: 0 10px;
}
.el-input {
  width: 150px;
}
.header {
    padding: 10px;
    background-color: #fff;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.buttonCon {
  margin-top: 10px;
  text-align: right;
}

.cell {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.formCon {
  max-height: 300px;
  overflow: auto;
}
.formitem {
  text-align: left;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.formCon .el-link {
  margin-right: 10px;
}
.formitem .name {
  width: 120px;
}
</style>


